.login-div form {
  border-top-right-radius: 20%;
  border-bottom-left-radius: 20%;
  background: linear-gradient(to right, #fffcdc, #d9a7c7);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #c6e5eb, #d9a7c7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.signup-div form {
  border-top-left-radius: 20%;
  border-bottom-right-radius: 20%;
  background: linear-gradient(to right, #fffcdc, #d9a7c7);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #d9a7c7, #c6e5eb); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.btndark {
  background: rgb(240, 234, 234);
}
